.WorkProjectWrapper{
    display: -ms-flexbox;
    display: flex;
    padding: 4vw 0;
}
.WorkProjectImg{
    display: block;
    width: 50%;
    -o-object-fit: contain;
       object-fit: contain;
}
.WorkProjectContent{
    display: block;
    padding: 0 0 0 2vw;
}

.WorkProjectContent h2{
    font-size: 1.6vw;
    margin-top: 0;
    font-weight: 500;
}
.WorkProjectContent p{
    display: block;
    padding: 0 0 0 3vw;
    line-height: 200%;
    letter-spacing: 0.04rem;
}

@media(max-width: 499px){
    .WorkProjectWrapper{
        display: block;
    }
    .WorkProjectImg{
        margin: 0 auto;
        width: 100%;
    }
}


